import React, { useState } from 'react';
import { Modal, TouchableHighlight, View, Image, StyleSheet } from 'react-native';
import UserGridMenu from './UserGridMenu';
import UserProfile from './UserProfile';

const list = [
    {
        id: 1,
        appLogo: 'data:image/svg+xml;base64,',
        appUrl: 'https://s3.amazonaws.com/uifaces/faces/twitter/ladylexy/128.jpg'
    },
    {
        id: 2,
        appLogo: 'data:image/png;base64,',
        appUrl: 'https://s3.amazonaws.com/uifaces/faces/twitter/ladylexy/128.jpg'
    },

];

const GridRow = () => {

    const callbackProfile = () => {
        setProfileVisible(false);
    }
    const callbackMenu = () => {
        setMenuVisible(false);
    }
    const [menuVisible, setMenuVisible] = useState(false);
    const [profileVisible, setProfileVisible] = useState(false);
    return (
        <View style={styles.container}>
            <TouchableHighlight
                activeOpacity={0.6}
                underlayColor="#DDDDDD"
                onPress={() => {
                    setMenuVisible(true);
                    setProfileVisible(false);
                }}>
                <View style={styles.gridRow}>
                    <Image
                        style={styles.gridIcon}
                        source={{ uri: list[0].appLogo }}
                    />
                </View>
            </TouchableHighlight>
            <TouchableHighlight
                activeOpacity={0.6}
                underlayColor="#DDDDDD"
                onPress={() => {
                    setProfileVisible(true);
                    setMenuVisible(false);
                }}>
                <View style={styles.gridRow}>
                    <Image
                        style={styles.avatar}
                        source={{ uri: list[1].appLogo }}
                    />
                </View>
            </TouchableHighlight>

                <View style={styles.modalView}>
                    <Modal
                        visible={menuVisible}
                        animationType="slide"
                        transparent={true}
                    >
                        <UserGridMenu onPressFunction={() => {callbackMenu}} />
                    </Modal>
                    <Modal
                        visible={profileVisible}
                        animationType="slide"
                        transparent={true}
                    >
                        <UserProfile onPressFunction={() => {callbackProfile}} />
                    </Modal>
                </View>
        </View>
    );
}


const iconWidth = 40;
const styles = StyleSheet.create({
    container: {
        flex: 1,
        flexDirection: 'row',
        justifyContent: 'center',
    },
    gridIcon: {
        width: iconWidth,
        height: iconWidth,
        marginBottom: 15
    },

    gridRow: {
        margin: 20,
    },
    avatar: {
        width: iconWidth,
        height: iconWidth,
        borderRadius: iconWidth / 2,
        marginBottom: 15
    },
    modalView: {
        margin: 20,
        backgroundColor: "white",
        borderRadius: 20,
        padding: 35,
        alignItems: "center",
        shadowColor: "#000",
        shadowOffset: {
          width: 0,
          height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 3.84,
        elevation: 5
      },

});
export default class NavProfile extends React.Component {
    render(){

        return (
            <View>
                <GridRow />
            </View>
        )
    }
    
}
